'use client';
import React, { useState, useEffect } from 'react';
import { useForm } from 'react-hook-form';
import { zodResolver } from '@hookform/resolvers/zod';
import { z } from 'zod';
import { useDebounce } from 'use-debounce';
import { motion } from 'framer-motion';
import {
    Plus,
    Edit,
    Trash2,
    User,
    UserX,
    UserCheck,
    Search,
    ChevronDown,
    ChevronUp,
    Filter
} from 'lucide-react';
import { Input } from '@/components/ui/input';
import { Button } from '@/components/ui/button';
import {
    Table,
    TableBody,
    TableCell,
    TableHead,
    TableHeader,
    TableRow
} from '@/components/ui/table';
import {
    DropdownMenu,
    DropdownMenuContent,
    DropdownMenuItem,
    DropdownMenuTrigger
} from '@/components/ui/dropdown-menu';
import UserFormDialog from "@/components/ui/Us";
import {
    Dialog,
    DialogContent,
    DialogHeader,
    DialogTitle,
    DialogDescription,
    DialogFooter
} from '@/components/ui/dialog';
import {
    Select,
    SelectContent,
    SelectItem,
    SelectTrigger,
    SelectValue
} from '@/components/ui/select';
import { Badge } from '@/components/ui/badge';
import { Card } from '@/components/ui/Card';
import { useToast } from '@/hooks/use-toast';
import { Pagination, PaginationContent, PaginationItem, PaginationPrevious, PaginationLink, PaginationNext } from '@/components/ui/pagination';

// Schema validation using Zod
const userSchema = z.object({
    id: z.string().optional(),
    name: z.string().min(2, "Name must be at least 3 characters"),
    email: z.string().email("Invalid email address"),
    role: z.enum(['student', 'host', 'admin']),
    status: z.enum(['active', 'disabled'])
});

type User = z.infer<typeof userSchema>;

const AdminUsersPage = () => {
    const { toast } = useToast();
    const [users, setUsers] = useState<User[]>([]);
    const [filteredUsers, setFilteredUsers] = useState<User[]>([]);
    const [selectedUser, setSelectedUser] = useState<User | null>(null);
    const [isEditModalOpen, setIsEditModalOpen] = useState(false);
    const [isDeleteModalOpen, setIsDeleteModalOpen] = useState(false);
    const [isCreateModalOpen, setIsCreateModalOpen] = useState(false);
    const [searchQuery, setSearchQuery] = useState('');
    const [debouncedQuery] = useDebounce(searchQuery, 300);
    const [roleFilter, setRoleFilter] = useState<string>('all');
    const [statusFilter, setStatusFilter] = useState<string>('all');
    const [sortField, setSortField] = useState<keyof User>('name');
    const [sortDirection, setSortDirection] = useState<'asc' | 'desc'>('asc');
    const [currentPage, setCurrentPage] = useState(1);
    const usersPerPage = 8;

    // Form setup
    const form = useForm<User>({
        resolver: zodResolver(userSchema),
        defaultValues: {
            name: '',
            email: '',
            role: 'student',
            status: 'active'
        }
    });

    // Simulate data fetching
    useEffect(() => {
        // In a real app, this would be an API call
        const mockUsers: User[] = Array.from({ length: 35 }, (_, i) => ({
            id: `user-${i}`,
            name: `User ${i + 1}`,
            email: `user${i + 1}@example.com`,
            role: i < 10 ? 'host' : 'student',
            status: i % 5 === 0 ? 'disabled' : 'active'
        }));

        setUsers(mockUsers);
        setFilteredUsers(mockUsers);
    }, []);
    // مكان useEffect القديم:
    useEffect(() => {
        const fetchUsers = async () => {
            try {
                const response = await fetch(`${process.env.NEXT_PUBLIC_API_URL}/admin/users`, {
                    method: 'GET',
                    credentials: 'include', // مهم جداً لإرسال الـ JWT cookie
                    headers: {
                        'Content-Type': 'application/json',
                    },
                });

                if (!response.ok) {
                    throw new Error('Failed to fetch users');
                }

                const data: User[] = await response.json();
                setUsers(data);
                setFilteredUsers(data);
            } catch (error :any) {
                toast({
                    title: 'Error',
                    description: error?.message || "An unexpected error occurred.",
                    className: 'bg-red-500 text-white border-0',
                });
            }
        };

        // fetchUsers();
    }, []);

    //Apply filters and sorting
    useEffect(() => {
        let result = [...users];

        // Apply search filter
        if (debouncedQuery) {
            result = result.filter(user =>
                user.name.toLowerCase().includes(debouncedQuery.toLowerCase()) ||
                user.email.toLowerCase().includes(debouncedQuery.toLowerCase())
            );
        }

        // Apply role filter
        if (roleFilter !== 'all') {
            result = result.filter(user => user.role === roleFilter);
        }

        // Apply status filter
        if (statusFilter !== 'all') {
            result = result.filter(user => user.status === statusFilter);
        }

        // Apply sorting
        result.sort((a, b) => {
            if (a[sortField] < b[sortField]) return sortDirection === 'asc' ? -1 : 1;
            if (a[sortField] > b[sortField]) return sortDirection === 'asc' ? 1 : -1;
            return 0;
        });

        setFilteredUsers(result);
        setCurrentPage(1); // Reset to first page when filters change
    }, [users, debouncedQuery, roleFilter, statusFilter, sortField, sortDirection]);

    // Handle sort
    const handleSort = (field: keyof User) => {
        if (sortField === field) {
            setSortDirection(sortDirection === 'asc' ? 'desc' : 'asc');
        } else {
            setSortField(field);
            setSortDirection('asc');
        }
    };

    // Handle delete
    const handleDelete = (user: User) => {
        setSelectedUser(user);
        setIsDeleteModalOpen(true);
    };

    // Handle disable/enable
    const handleToggleStatus = (user: User) => {
        const updatedUsers = users.map(u =>
            u.id === user.id ? { ...u, status: u.status === 'active' ? 'disabled' : 'active' } : u
        );

        setUsers(updatedUsers);

        toast({
            title: "User Status Updated",
            description: `${user.name}'s status has been changed to ${user.status === 'active' ? 'disabled' : 'active'}`,
            className: "bg-blue-500 text-white border-0",
        });
    };

    // Handle form submission
    const onSubmit = (data: User) => {
        if (isEditModalOpen && selectedUser) {
            // Update existing user
            const updatedUsers = users.map(user =>
                user.id === selectedUser.id ? { ...user, ...data } : user 
            );
            setUsers(updatedUsers);

            toast({
                title: "User Updated",
                description: `${data.name}'s information has been updated`,
                className: "bg-green-500 text-white border-0",
            });
        } else {
            // Create new user
            const newUser = {
                ...data,
                id: `user-${users.length}`
            };
            setUsers([...users, newUser]);

            toast({
                title: "User Created",
                description: `${data.name} has been added to the system`,
                className: "bg-green-500 text-white border-0",
            });
        }

        setIsEditModalOpen(false);
        setIsCreateModalOpen(false);
        form.reset();
    };

    // Handle delete confirmation
    const confirmDelete = () => {
        if (!selectedUser) return;

        const updatedUsers = users.filter(user => user.id !== selectedUser.id);
        setUsers(updatedUsers);

        toast({
            title: "User Deleted",
            description: `${selectedUser.name} has been removed from the system`,
            className: "bg-red-500 text-white border-0",
        });

        setIsDeleteModalOpen(false);
        setSelectedUser(null);
    };

    // Pagination logic
    const indexOfLastUser = currentPage * usersPerPage;
    const indexOfFirstUser = indexOfLastUser - usersPerPage;
    const currentUsers = filteredUsers.slice(indexOfFirstUser, indexOfLastUser);
    const totalPages = Math.ceil(filteredUsers.length / usersPerPage);

    // Render sort indicator
    const renderSortIndicator = (field: keyof User) => {
        if (sortField === field) {
            return sortDirection === 'asc' ? <ChevronUp className="ml-1 h-4 w-4" /> : <ChevronDown className="ml-1 h-4 w-4" />;
        }
        return null;
    };

    return (
        <div className="min-h-screen bg-gradient-to-br from-indigo-50 to-blue-100 p-6">
            <motion.div
                initial={{ opacity: 0, y: 20 }}
                animate={{ opacity: 1, y: 0 }}
                transition={{ duration: 0.5 }}
                className="max-w-7xl mx-auto"
            >
                <div className="flex flex-col md:flex-row justify-between items-start md:items-center mb-8 gap-4">
                    <motion.h1
                        initial={{ opacity: 0 }}
                        animate={{ opacity: 1 }}
                        transition={{ delay: 0.1 }}
                        className="text-3xl font-bold text-gray-800"
                    >
                        User Management
                    </motion.h1>
                    <motion.div
                        initial={{ opacity: 0 }}
                        animate={{ opacity: 1 }}
                        transition={{ delay: 0.2 }}
                    >
                        <Button
                            className="bg-gradient-to-r from-purple-600 to-indigo-600 hover:from-purple-700 hover:to-indigo-700"
                            onClick={() => setIsCreateModalOpen(true)}
                        >
                            <Plus className="mr-2 h-4 w-4" /> Add New User
                        </Button>
                    </motion.div>
                </div>

                {/* Filters and Search */}
                <motion.div
                    initial={{ opacity: 0 }}
                    animate={{ opacity: 1 }}
                    transition={{ delay: 0.3 }}
                    className="grid grid-cols-1 md:grid-cols-4 gap-4 mb-6"
                >
                    <div className="relative">
                        <Search className="absolute left-3 top-3 text-gray-400 h-4 w-4" />
                        <Input
                            placeholder="Search users..."
                            value={searchQuery}
                            onChange={(e) => setSearchQuery(e.target.value)}
                            className="pl-10"
                        />
                    </div>

                    <Select value={roleFilter} onValueChange={setRoleFilter}>
                        <SelectTrigger className="w-full">
                            <div className="flex items-center">
                                <User className="mr-2 h-4 w-4" />
                                <span>{roleFilter === 'all' ? 'All Roles' : roleFilter}</span>
                            </div>
                        </SelectTrigger>
                        <SelectContent>
                            <SelectItem value="all">All Roles</SelectItem>
                            <SelectItem value="host">Host</SelectItem>
                            <SelectItem value="student">Student</SelectItem>
                        </SelectContent>
                    </Select>

                    <Select value={statusFilter} onValueChange={setStatusFilter}>
                        <SelectTrigger className="w-full">
                            <div className="flex items-center">
                                <Filter className="mr-2 h-4 w-4" />
                                <span>{statusFilter === 'all' ? 'All Statuses' : statusFilter}</span>
                            </div>
                        </SelectTrigger>
                        <SelectContent>
                            <SelectItem value="all">All Statuses</SelectItem>
                            <SelectItem value="active">Active</SelectItem>
                            <SelectItem value="disabled">Disabled</SelectItem>
                        </SelectContent>
                    </Select>

                    <DropdownMenu>
                        <DropdownMenuTrigger asChild>
                            <Button variant="outline" className="w-full">
                                <span className="flex items-center">
                                    Sort by: {sortField} {renderSortIndicator(sortField)}
                                </span>
                            </Button>
                        </DropdownMenuTrigger>
                        <DropdownMenuContent>
                            <DropdownMenuItem onClick={() => handleSort('name')}>
                                Name {sortField === 'name' && (sortDirection === 'asc' ? '↑' : '↓')}
                            </DropdownMenuItem>
                            <DropdownMenuItem onClick={() => handleSort('email')}>
                                Email {sortField === 'email' && (sortDirection === 'asc' ? '↑' : '↓')}
                            </DropdownMenuItem>
                            <DropdownMenuItem onClick={() => handleSort('role')}>
                                Role {sortField === 'role' && (sortDirection === 'asc' ? '↑' : '↓')}
                            </DropdownMenuItem>
                        </DropdownMenuContent>
                    </DropdownMenu>
                </motion.div>

                {/* Users Table */}
                <motion.div
                    initial={{ opacity: 0 }}
                    animate={{ opacity: 1 }}
                    transition={{ delay: 0.4 }}
                >
                    <Card className="overflow-hidden p-4">
                        <Table>
                            <TableHeader className="bg-gray-100">
                                <TableRow>
                                    <TableHead className="w-[150px]">Name</TableHead>
                                    <TableHead>Email</TableHead>
                                    <TableHead>Role</TableHead>
                                    <TableHead>Status</TableHead>
                                    <TableHead className="text-right">Actions</TableHead>
                                </TableRow>
                            </TableHeader>
                            <TableBody>
                                {currentUsers.length === 0 ? (
                                    <TableRow>
                                        <TableCell colSpan={5} className="text-center py-8">
                                            <div className="flex flex-col items-center justify-center">
                                                <UserX className="h-12 w-12 text-gray-400 mb-2" />
                                                <p className="text-gray-500">No users found matching your criteria</p>
                                            </div>
                                        </TableCell>
                                    </TableRow>
                                ) : (
                                    currentUsers.map((user) => (
                                        <motion.tr
                                            key={user.id}
                                            initial={{ opacity: 0, y: 10 }}
                                            animate={{ opacity: 1, y: 0 }}
                                            exit={{ opacity: 0 }}
                                            transition={{ duration: 0.3 }}
                                            className="border-b hover:bg-gray-50"
                                        >
                                            <TableCell className="font-medium">{user.name}</TableCell>
                                            <TableCell>{user.email}</TableCell>
                                            <TableCell>
                                                <Badge
                                                    variant={
                                                        user.role === 'admin' ? 'destructive' :
                                                            user.role === 'host' ? 'default' : 'secondary'
                                                    }
                                                    className="capitalize"
                                                >
                                                    {user.role}
                                                </Badge>
                                            </TableCell>
                                            <TableCell>
                                                <Badge
                                                    variant={user.status === 'active' ? 'success' : 'warning'}
                                                    className="capital"
                                                >
                                                    {user.status}
                                                </Badge>
                                            </TableCell>
                                            <TableCell className="text-right">
                                                <div className="flex justify-end gap-2">
                                                    <Button
                                                        variant="outline"
                                                        size="icon"
                                                        onClick={() => handleToggleStatus(user)}
                                                        className={
                                                            user.status === 'active'
                                                                ? "text-orange-600 hover:bg-orange-50"
                                                                : "text-green-600 hover:bg-green-50"
                                                        }
                                                    >
                                                        {user.status === 'active' ?
                                                            <UserX className="h-4 w-4" /> :
                                                            <UserCheck className="h-4 w-4" />
                                                        }
                                                    </Button>
                                                    <Button
                                                        variant="outline"
                                                        size="icon"
                                                        onClick={() => handleDelete(user)}
                                                        className="text-red-600 hover:bg-red-50"
                                                    >
                                                        <Trash2 className="h-4 w-4" />
                                                    </Button>
                                                </div>
                                            </TableCell>
                                        </motion.tr>
                                    ))
                                )}
                            </TableBody>
                        </Table>
                    </Card>
                </motion.div>

                {/* Pagination */}
                {totalPages > 1 && (
                    <motion.div
                        initial={{ opacity: 0 }}
                        animate={{ opacity: 1 }}
                        transition={{ delay: 0.5 }}
                        className="mt-8 flex justify-center"
                    >
                        <Pagination>
                            <PaginationContent>
                                <PaginationItem>
                                    <PaginationPrevious
                                        onClick={() => setCurrentPage(prev => Math.max(1, prev - 1))}
                                        disabled={currentPage === 1}
                                    />
                                </PaginationItem>

                                {Array.from({ length: Math.min(5, totalPages) }, (_, i) => {
                                    const startPage = Math.max(1, Math.min(currentPage - 2, totalPages - 4));
                                    return startPage + i;
                                }).map(page => (
                                    <PaginationItem key={page}>
                                        <PaginationLink
                                            isActive={page === currentPage}
                                            onClick={() => setCurrentPage(page)}
                                        >
                                            {page}
                                        </PaginationLink>
                                    </PaginationItem>
                                ))}

                                <PaginationItem>
                                    <PaginationNext
                                        onClick={() => setCurrentPage(prev => Math.min(totalPages, prev + 1))}
                                        disabled={currentPage === totalPages}
                                    />
                                </PaginationItem>
                            </PaginationContent>
                        </Pagination>
                    </motion.div>
                )}

                {/* Stats */}
                <motion.div
                    initial={{ opacity: 0 }}
                    animate={{ opacity: 1 }}
                    transition={{ delay: 0.6 }}
                    className="grid grid-cols-1 md:grid-cols-3 gap-4 mt-8"
                >
                    <div className="bg-white rounded-lg p-6 shadow-sm border border-gray-200">
                        <div className="flex items-center">
                            <div className="bg-blue-100 p-3 rounded-full mr-4">
                                <User className="h-6 w-6 text-blue-600" />
                            </div>
                            <div>
                                <p className="text-gray-500">Total Users</p>
                                <p className="text-2xl font-bold">{users.length}</p>
                            </div>
                        </div>
                    </div>

                    <div className="bg-white rounded-lg p-6 shadow-sm border border-gray-200">
                        <div className="flex items-center">
                            <div className="bg-green-100 p-3 rounded-full mr-4">
                                <UserCheck className="h-6 w-6 text-green-600" />
                            </div>
                            <div>
                                <p className="text-gray-500">Active Users</p>
                                <p className="text-2xl font-bold">{users.filter(u => u.status === 'active').length}</p>
                            </div>
                        </div>
                    </div>

                    <div className="bg-white rounded-lg p-6 shadow-sm border border-gray-200">
                        <div className="flex items-center">
                            <div className="bg-purple-100 p-3 rounded-full mr-4">
                                <UserX className="h-6 w-6 text-purple-600" />
                            </div>
                            <div>
                                <p className="text-gray-500">Disabled Users</p>
                                <p className="text-2xl font-bold">{users.filter(u => u.status === 'disabled').length}</p>
                            </div>
                        </div>
                    </div>
                </motion.div>
            </motion.div>


            {/* Create User Modal */}
            <Dialog open={isCreateModalOpen} onOpenChange={setIsCreateModalOpen}>
                <DialogContent className="sm:max-w-[500px]">
                    <DialogHeader>
                        <DialogTitle>Create New User</DialogTitle>
                        <DialogDescription>
                            Add a new user to the system. Fill in the details below.
                        </DialogDescription>
                    </DialogHeader>
                    <form onSubmit={form.handleSubmit(onSubmit)} className="space-y-4">
                        <div>
                            <label className="block text-sm font-medium mb-1">Name</label>
                            <Input
                                {...form.register('name')}
                                placeholder="Full name"
                            />
                            {form.formState.errors.name && (
                                <p className="text-red-500 text-sm mt-1">{form.formState.errors.name.message}</p>
                            )}
                        </div>

                        <div>
                            <label className="block text-sm font-medium mb-1">Email</label>
                            <Input
                                {...form.register('email')}
                                placeholder="Email address"
                            />
                            {form.formState.errors.email && (
                                <p className="text-red-500 text-sm mt-1">{form.formState.errors.email.message}</p>
                            )}
                        </div>

                        <div className="grid grid-cols-2 gap-4">
                            <div>
                                <label className="block text-sm font-medium mb-1">Role</label>
                                <Select
                                    value={form.watch('role')}
                                    onValueChange={(value) => form.setValue('role', value as any)}
                                >
                                    <SelectTrigger>
                                        <SelectValue placeholder="Select role" />
                                    </SelectTrigger>
                                    <SelectContent>
                                        <SelectItem value="admin">Admin</SelectItem>
                                        <SelectItem value="host">Host</SelectItem>
                                        <SelectItem value="student">Student</SelectItem>
                                    </SelectContent>
                                </Select>
                            </div>

                            <div>
                                <label className="block text-sm font-medium mb-1">Status</label>
                                <Select
                                    value={form.watch('status')}
                                    onValueChange={(value) => form.setValue('status', value as any)}
                                >
                                    <SelectTrigger>
                                        <SelectValue placeholder="Select status" />
                                    </SelectTrigger>
                                    <SelectContent>
                                        <SelectItem value="active">Active</SelectItem>
                                        <SelectItem value="disabled">Disabled</SelectItem>
                                    </SelectContent>
                                </Select>
                            </div>
                        </div>

                        <DialogFooter className="mt-4">
                            <Button
                                variant="outline"
                                onClick={() => setIsCreateModalOpen(false)}
                            >
                                Cancel
                            </Button>
                            <Button type="submit">Create User</Button>
                        </DialogFooter>
                    </form>
                </DialogContent>
            </Dialog>

            {/* Delete Confirmation Modal */}
            <Dialog open={isDeleteModalOpen} onOpenChange={setIsDeleteModalOpen}>
                <DialogContent className="sm:max-w-[425px]">
                    <DialogHeader>
                        <DialogTitle>Confirm Deletion</DialogTitle>
                        <DialogDescription>
                            Are you sure you want to delete this user? This action cannot be undone.
                        </DialogDescription>
                    </DialogHeader>
                    <div className="py-4">
                        {selectedUser && (
                            <div className="flex items-center gap-4">
                                <div className="bg-gray-200 border-2 border-dashed rounded-xl w-16 h-16" />
                                <div>
                                    <h4 className="font-semibold">{selectedUser.name}</h4>
                                    <p className="text-gray-500">{selectedUser.email}</p>
                                </div>
                            </div>
                        )}
                    </div>
                    <DialogFooter>
                        <Button
                            variant="outline"
                            onClick={() => setIsDeleteModalOpen(false)}
                        >
                            Cancel
                        </Button>
                        <Button
                            variant="destructive"
                            onClick={confirmDelete}
                        >
                            Delete Permanently
                        </Button>
                    </DialogFooter>
                </DialogContent>
            </Dialog>

            {/* Edit User Modal */}
            <UserFormDialog
                open={isEditModalOpen}
                onOpenChange={setIsEditModalOpen}
                title="Edit User"
                description="Update user information below. Click save when you're done."
                confirmLabel="Save Changes"
                form={form}
                onSubmit={onSubmit}
            />

            <UserFormDialog
                open={isCreateModalOpen}
                onOpenChange={setIsCreateModalOpen}
                title="Create New User"
                description="Add a new user to the system. Fill in the details below."
                confirmLabel="Create User"
                form={form}
                onSubmit={onSubmit}
            />
        </div>
    );
};

export default AdminUsersPage;